import React from "react"
import $ from "jquery"
class UserGithub extends React.Compoent{
    constructor(props){
        super(props);
        this.state={
            username:'',
            githubUrl:'',
            avataUrl:'',
        }
    }
    compoentDidMount(){
        $.get(this.props.source,(result)=>{
            console.log(result);
            const data=result;
            if(data){
                this.setState({
                    username:data.name,
                    githubUrl:data.html.url,
                    avataUrl:data.avatar_url,
                });
            }
        });
    }
    render(){
        return(
            <div>
                <h3>{this.state.username}</h3>
                <img src="roger1.jpg"/>
                <a href={this.state.githubUrl}>Github Link</a>
            </div>
        )
    }
}
export default UserGithub;
